/** == Elements == **/
/** == Mq == **/
.leyka-rbk-pre-submit-step {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-flow: row wrap;
  flex-flow: row wrap;
  -webkit-box-pack: space-evenly;
  -ms-flex-pack: space-evenly;
  justify-content: space-evenly;
  height: 100%;
}

.leyka-rbk-pre-submit-step .leyka-rbk-final-submit-buttons {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-flow: row wrap;
  flex-flow: row wrap;
  -webkit-box-pack: space-evenly;
  -ms-flex-pack: space-evenly;
  justify-content: space-evenly;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.leyka-rbk-pre-submit-step .leyka-rbk-final-submit-buttons button {
  -webkit-box-flex: 1;
  -ms-flex: 1 -1 auto;
  flex: 1 -1 auto;
  text-align: center;
  font-family: Roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-weight: 400;
  font-style: normal;
  font-size: 13px;
  line-height: 18px;
  display: inline-block;
  text-transform: uppercase;
  color: #ffffff;
  background: var(--color-main);
  padding: 10px 26px;
  text-align: center;
  border: none;
  -webkit-box-shadow: none;
  box-shadow: none;
  border-radius: 23px;
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
  cursor: pointer;
  border-radius: 27px;
  padding: 12px 26px;
  min-width: 200px;
  font-size: 14px;
}

.leyka-rbk-pre-submit-step .leyka-rbk-final-submit-buttons button:visited {
  color: #ffffff;
}

.leyka-rbk-pre-submit-step .leyka-rbk-final-submit-buttons button:hover, .leyka-rbk-pre-submit-step .leyka-rbk-final-submit-buttons button:focus {
  background: var(--color-main-dark);
}
